import React, { useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  IconButton,
  Badge,
  Container,
  useTheme,
  useMediaQuery,
  alpha,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider
} from '@mui/material';
import { useAuth } from '../contexts/AuthContext';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Link } from 'react-router-dom';
import PersonIcon from '@mui/icons-material/Person';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import SupportIcon from '@mui/icons-material/Support';
import MenuIcon from '@mui/icons-material/Menu';
import AccessibilityNewIcon from '@mui/icons-material/AccessibilityNew';
import { useSelector } from 'react-redux';
import { RootState } from '../store';
import api from '../api/axios';
import LogoutIcon from '@mui/icons-material/Logout';
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';

const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const navigate = useNavigate();
  const { isAuthenticated, isAdmin, logout, user } = useAuth();
  const { token } = useAuth();
  const cartItems = useSelector((state: RootState) => state.cart.items);
  const cartItemsCount = cartItems.reduce((total, item) => total + item.quantity, 0);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleLogout = () => {
    logout();
    setMobileMenuOpen(false);
  };

  const handleMobileMenuToggle = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };

  const menuItems = [
    { text: 'Профиль', icon: <PersonIcon />, path: '/profile', auth: true },
    { text: 'Админ-панель', icon: <AdminPanelSettingsIcon />, path: '/admin', auth: true, admin: true },
    { text: 'Поддержка', icon: <SupportIcon />, path: '/support' },
    { text: 'Настройки доступности', icon: <AccessibilityNewIcon />, path: '/accessibility' }
  ];

  const renderMenuItems = () => {
    return menuItems
      .filter(item => !item.auth || (item.auth && token))
      .filter(item => !item.admin || (item.admin && user?.role === 'ADMIN'))
      .map((item) => (
        <ListItem
          key={item.text}
          onClick={() => {
            navigate(item.path);
            setMobileMenuOpen(false);
          }}
          sx={{
            py: 1.5,
            '&:hover': {
              backgroundColor: alpha(theme.palette.primary.main, 0.1)
            }
          }}
        >
          <ListItemIcon sx={{ color: theme.palette.primary.main }}>{item.icon}</ListItemIcon>
          <ListItemText primary={item.text} />
        </ListItem>
      ));
  };

  const renderMobileMenu = () => (
    <Drawer
      anchor="right"
      open={mobileMenuOpen}
      onClose={handleMobileMenuToggle}
      PaperProps={{
        sx: {
          width: 280,
          backgroundColor: theme.palette.background.paper,
          boxShadow: '0 0 20px rgba(0,0,0,0.1)'
        }
      }}
    >
      <List sx={{ width: '100%' }}>
        {token ? (
          <>
            <ListItem sx={{ 
              backgroundColor: alpha(theme.palette.primary.main, 0.05),
              py: 2
            }}>
              <ListItemText
                primary={user?.name || 'Пользователь'}
                secondary={user?.email}
                primaryTypographyProps={{
                  fontWeight: 600,
                  color: theme.palette.primary.main
                }}
              />
            </ListItem>
            <Divider />
            {renderMenuItems()}
            <Divider />
            <ListItem 
              button 
              onClick={handleLogout}
              sx={{
                color: theme.palette.error.main,
                py: 1.5,
                '&:hover': {
                  backgroundColor: alpha(theme.palette.error.main, 0.1)
                }
              }}
            >
              <ListItemText primary="Выйти" />
            </ListItem>
          </>
        ) : (
          <>
            <ListItem 
              button 
              onClick={() => {
                navigate('/login');
                setMobileMenuOpen(false);
              }}
              sx={{
                py: 1.5,
                '&:hover': {
                  backgroundColor: alpha(theme.palette.primary.main, 0.1)
                }
              }}
            >
              <ListItemText primary="Войти" />
            </ListItem>
            <ListItem 
              button 
              onClick={() => {
                navigate('/register');
                setMobileMenuOpen(false);
              }}
              sx={{
                py: 1.5,
                '&:hover': {
                  backgroundColor: alpha(theme.palette.primary.main, 0.1)
                }
              }}
            >
              <ListItemText primary="Регистрация" />
            </ListItem>
          </>
        )}
      </List>
    </Drawer>
  );

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <AppBar
        position="static"
        sx={{
          backgroundColor: theme.palette.primary.main,
          borderBottom: '2px solid rgba(255, 255, 255, 0.1)',
          '& *': {
            color: 'white'
          }
        }}
      >
        <Toolbar>
          <Typography
            variant="h6"
            component={RouterLink}
            to="/"
            sx={{
              flexGrow: 1,
              textDecoration: 'none',
              color: 'inherit',
              fontWeight: 700,
              fontSize: '1.5rem',
              letterSpacing: '0.5px',
              '&:hover': {
                color: 'inherit',
                opacity: 0.9
              }
            }}
          >
            Магазин Сафоновой
          </Typography>

          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            {!isAuthenticated ? (
              <>
                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/products"
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Товары
                </Button>
                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/login"
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Войти
                </Button>
                <Button
                  variant="outlined"
                  color="inherit"
                  component={RouterLink}
                  to="/register"
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    borderColor: 'white',
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)',
                      borderColor: 'white'
                    }
                  }}
                >
                  Регистрация
                </Button>
              </>
            ) : (
              <>
                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/products"
                  startIcon={<ShoppingBagIcon />}
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Товары
                </Button>
                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/accessibility"
                  startIcon={<AccessibilityNewIcon />}
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Доступность
                </Button>

                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/cart"
                  startIcon={<ShoppingCartIcon />}
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Корзина
                  <Badge 
                    badgeContent={cartItemsCount} 
                    color="error"
                    sx={{ ml: 1 }}
                  />
                </Button>

                <Button
                  color="inherit"
                  component={RouterLink}
                  to="/profile"
                  startIcon={<PersonIcon />}
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Профиль
                </Button>

                {isAdmin && (
                  <Button
                    color="inherit"
                    component={RouterLink}
                    to="/admin"
                    startIcon={<AdminPanelSettingsIcon />}
                    sx={{
                      px: 2,
                      py: 1,
                      borderRadius: 2,
                      '&:hover': {
                        backgroundColor: 'rgba(255, 255, 255, 0.1)'
                      }
                    }}
                  >
                    Админ
                  </Button>
                )}

                <Button
                  color="inherit"
                  onClick={handleLogout}
                  startIcon={<LogoutIcon />}
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.1)'
                    }
                  }}
                >
                  Выйти
                </Button>
              </>
            )}
          </Box>
        </Toolbar>
      </AppBar>
      <Box 
        sx={{ 
          minHeight: 'calc(100vh - 64px)',
          backgroundColor: 'var(--background-color, rgba(245, 245, 245, 0.5))',
          py: 4
        }}
      >
        <Container maxWidth="lg" sx={{ height: '100%' }}>
          {children}
        </Container>
      </Box>
    </Box>
  );
};

export default Layout; 